@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Poppins:wght@300;400&display=swap');
html{
  scroll-behavior: smooth;
}
body {
  background-color: #2B2B2B;
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo{
  font-family: 'Bebas Neue', cursive;
}

.project{
  width: 564px;
}

.top{
  font-family: 'Bebas Neue', cursive;
}

.top p{
  font-family: 'Poppins', sans-serif;
}

.btn{
  background-color: #B20C0C;
  font-family: 'Bebas Neue', cursive;
}

.midTop{
  background-color: #252323;
}

.contentTop h1{
  font-family: 'Bebas Neue', cursive;
}


.bg{
  background: url("./assets/image1.png");
  background-size: cover;
}

.bg2{
  background: url("./assets/image2.png");
  background-size: cover;
}

.bg3{
  background: url("./assets/image3.png");
  background-size: cover;
}


.box h1{
  font-family: 'Bebas Neue', cursive;
}

.right{
  font-family: 'Bebas Neue', cursive;
}
.span{
  color: #B20C0C;
}


.bg-footer{
  background-color: #252323;
}
.modal{
  position: fixed;
  overflow: auto;
}

.modal h2{
  font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
}

.modal p {
  font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
}

.modal input{
  font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
}

.modal textarea{
  font-family: Bebas Neue;
font-style: normal;
font-weight: normal;
}
.bg-modal{
  background-color: #252323;
}

.bg-modal-2{
  background-color: #2B2B2B;
}

.text-gray-000 {
  --tw-text-opacity: 1;
  color: rgba(141, 141, 141, var(--tw-text-opacity));
}

.placeholder-gray-000::placeholder {
  --tw-placeholder-opacity: 1;
  color: rgba(141, 141, 141, var(--tw-text-opacity));
}


@media (max-width: 640px) {
  .project{
    width: 364px;
  }
}